<template>
	<ul class="xz">
			<li v-for="item in datas">
				<list :datas="item"></list>
			</li>
		</ul>
</template>

<script>
import list from '@/components/List';
export default {
	data() {
		return {
			datas: [{name:"衣服",src:"goods1.jpg",price:"100"},{name:"裤子",src:"goods1.jpg",price:"80"}]
		};
	},
	mounted() {
		fetch('/selectGoods').then(function(e) {
			return e.json();
		}).then(function(e) {
			this.datas = e;
		}.bind(this));
},
components:{
	list
}
}</script>

<style>
	*{
			font-style: normal;
			padding: 0;
			margin: 0;
			list-style: none;
			-webkit-user-select: none;
		}
	.xz{
			display: flex;
			flex-wrap: wrap;
			/*justify-content: space-between;*/
		}
		.xz li{
      border: 1px solid #ccc;
      width: 23%;
      margin-right: 2%;
      height: 300px;
      margin-bottom: 20px;
      transition: all 0.5s;
		}
		.xz li:hover{
			border: 1px solid #f66;
		}
		a{
			font-size: 20px;
		}
		.title{
			font-size: 20px;
			text-align: center;
		}
		.xz img{
			display: block;
			width: 100%;
			height: 150px;
		}
		.text{
			box-sizing: border-box;
			margin-top: 50px;
			font-size: 20px;
			padding: 0px 10px;
			display:flex;
			justify-content: space-between;
			align-items: center;
		}
		.price{
			color: red;
			font-size: 20px;
		}
		.carIco{
      background: #00B7FF;
      display: block;
      width: 110px;
      height: 25px;
      font-size: 16px;

      border-radius:7px;
      text-align: center;
      line-height: 25px;
      color: white;
      cursor: pointer;
		}
</style>



