<template>
  <div>
    <h1 class="title">{{datas.name}}</h1>
    <img src="static/img/djhy3.png">
    <div class="text">
      <span class="price"><em style="font-size: 30px;margin-right: 2px;">{{datas.price}}</em>¥</span>
      <div class="carIco" @click="add(datas.id)">加入购物车</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'List',
  methods: {
    add(id) {
      fetch('/add/'+id).then(function(e) {
        return e.text()
      }).then(function(e) {
        if(e == "ok") {
          alert("选购成功!");
        }
      });
    }
  },
  props: ["datas"]
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



